    --input-bc: rgb(196, 190, 190);
}

body{
    background-color: rgb(225, 223, 223);
    margin: 0;
}

#container{
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 5.5fr;
}

.sidebar{
    background-color: rgb(57, 185, 195);
    height: 100vh;
    padding: 0 8px;
}

.sidebar-header{
    display: flex;
    height: 10%;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgb(35, 154, 163);
}

button.add-project{
    height: 36px;
    width: 36px;
    border-radius: 10px;
    margin-right: 12px;
    font-size: 1.5rem;
    font-weight: 700;
    padding: 2px 8px;
}


.project-box{
    display: flex;
    justify-content: space-between;
    margin-top: 24px;
}

.project{
    cursor: pointer;
    font-size: 1.15rem;
    font-weight: 600;
}

.content{
    padding: 64px 50px;
}

.task-box{
    height: 80px;
    border-bottom: 1px solid rgb(202, 197, 197);
    display: grid;
    grid-auto-flow: column;
    grid-template-columns: min-content 1fr 40px;
}

.left-container{
    display: flex;
    align-items: center;
}

.mid-container{
    display: flex;
    align-items: center;
}

.mid-container > *{
    margin-left: 32px;
}

.right-container{
    justify-self: end;
    align-self: center;
}

.task-name{
    font-size: 24px;
}

.task-date{
    color: rgb(35, 181, 35);
    font-size: 16px;
    font-weight: 700;
}

.add-task{
    display: flex;
    align-items: center;
    background-color: inherit;
    gap: 16px;
    font-size: 22px;
    color: rgb(96, 95, 95);
    padding: 0;
}

spin.add-task{
    font-size: 3rem;
    color: rgb(194, 84, 6);
    margin-bottom: 4px;
}

.form-grid-container{
    height: 200px;
    display: grid;
    align-items: center;
}

dialog{
    background-color: rgb(243, 242, 236);
    border-radius: 1em;
    border: 6px solid rgb(246, 181, 61);
}

dialog button, dialog input{
    height: 40px;
    width: 250px;
    border-radius: 6px;
    border: 0;
    font-size: 20px;
}

#add-task-dialog label,
#edit-task-dialog label{
    display: block;
}

#add-task-dialog input,
#edit-task-dialog input{
    margin-bottom: 12px;
}

dialog input{
    background-color: var(--input-bc);
    width: 358px;
}

.confirm, .cancel{
    font-weight: 700;
    color: rgb(200, 199, 199);
}

button.confirm{
    background-color: rgb(30, 110, 0);
}

button.cancel{
    background-color: rgb(234, 60, 60);
}

#add-task-dialog .buttons,
#edit-task-dialog .buttons{
    margin-top: 24px;
}

.form-grid-container p:first-of-type{
    display: flex;
    align-items: center;
    gap: 18px;
}

select{
    width: 250px;
    height: 40px;
    border-radius: 6px;
    border: 0;
    background-color: var(--input-bc);
    font-size: 14px;
    font-weight: 700;
    text-align: center;
}

select option{
    background-color: white;
    font-weight: 700;
}

option[value="low"]{
    color: blue;
}

option[value="medium"]{
    color: orange;
}

option[value="high"]{
    color: red;
}

label{
    font-size: 18px;
    font-weight: 700;
}

input:hover{
    background-color: rgb(207, 200, 200);
}

select:hover{
    background-color: rgb(207, 200, 200);
}

.confirm:hover{
    background-color: rgb(36, 120, 6);
    color: black;
}

.cancel:hover{
    background-color: rgb(251, 67, 67);
    color: black;
}

img{
    width: 20px;
    cursor: pointer;
}

input[type="checkbox"]{
    width: 20px;
    height: 20px;
}

.priority{
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: inherit;
}

.low{
    border: 2px solid blue;
}

.medium{
    border: 2px solid orange;
}

.high{
    border: 2px solid red;
}

button{
    cursor: pointer;
    border: 0;
}

button:focus{
    outline: none;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,8BAA8B;AAClC;;AAEA;IACI,oCAAoC;IACpC,SAAS;AACb;;AAEA;IACI,aAAa;IACb,aAAa;IACb,gCAAgC;AACpC;;AAEA;IACI,mCAAmC;IACnC,aAAa;IACb,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,WAAW;IACX,8BAA8B;IAC9B,mBAAmB;IACnB,0CAA0C;AAC9C;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,mBAAmB;IACnB,kBAAkB;IAClB,iBAAiB;IACjB,gBAAgB;IAChB,gBAAgB;AACpB;;;AAGA;IACI,aAAa;IACb,8BAA8B;IAC9B,gBAAgB;AACpB;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,YAAY;IACZ,2CAA2C;IAC3C,aAAa;IACb,sBAAsB;IACtB,2CAA2C;AAC/C;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,iBAAiB;IACjB,kBAAkB;AACtB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,uBAAuB;IACvB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,yBAAyB;IACzB,SAAS;IACT,eAAe;IACf,sBAAsB;IACtB,UAAU;AACd;;AAEA;IACI,eAAe;IACf,sBAAsB;IACtB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,aAAa;IACb,mBAAmB;AACvB;;AAEA;IACI,oCAAoC;IACpC,kBAAkB;IAClB,mCAAmC;AACvC;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,SAAS;IACT,eAAe;AACnB;;AAEA;;IAEI,cAAc;AAClB;;AAEA;;IAEI,mBAAmB;AACvB;;AAEA;IACI,iCAAiC;IACjC,YAAY;AAChB;;AAEA;IACI,gBAAgB;IAChB,yBAAyB;AAC7B;;AAEA;IACI,iCAAiC;AACrC;;AAEA;IACI,kCAAkC;AACtC;;AAEA;;IAEI,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,SAAS;AACb;;AAEA;IACI,YAAY;IACZ,YAAY;IACZ,kBAAkB;IAClB,SAAS;IACT,iCAAiC;IACjC,eAAe;IACf,gBAAgB;IAChB,kBAAkB;AACtB;;AAEA;IACI,uBAAuB;IACvB,gBAAgB;AACpB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,oCAAoC;AACxC;;AAEA;IACI,iCAAiC;IACjC,YAAY;AAChB;;AAEA;IACI,kCAAkC;IAClC,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,yBAAyB;AAC7B;;AAEA;IACI,sBAAsB;AAC1B;;AAEA;IACI,wBAAwB;AAC5B;;AAEA;IACI,qBAAqB;AACzB;;AAEA;IACI,eAAe;IACf,SAAS;AACb;;AAEA;IACI,aAAa;AACjB","sourcesContent":[":root{\n    --input-bc: rgb(196, 190, 190);\n}\n\nbody{\n    b